import { useState } from 'react';
import './App.css';
import Navbar from './components/Navbar';
// import About from './components/About';
import Home from './components/Home';
import React from "react";
// import {
//   BrowserRouter as Router,
//   Routes,
//   Route
// } from "react-router-dom";

function App() {
  const [Mode, setMode] = useState('light'); // Whether dark mode is enabled or not

  const toggleMode = () => {
    if(Mode === 'light'){
      setMode('dark')
      document.body.style.backgroundColor = '#282c30d6';
      document.body.style.color = 'white';
    }
    else{
      setMode('light')
      document.body.style.backgroundColor = 'white';
      document.body.style.color = 'black';
    }
  }
  return (
    <>
      <Navbar mode={Mode} toggleMode={toggleMode} />
      <div className="container my-3">
        <Home heading="Try TextUtils - Word Counter, Character Counter, Remove extra blank spaces"/>
      </div>
      {/* <Router>
      <div className="container my-3">
        <Routes>
          <Route exact path="/about" element={<About mode={Mode} />}>  
          </Route>
          <Route exact path="/" element={<Home heading="Try TextUtils - Word Counter, Character Counter, Remove extra blank spaces"/>}>    
          </Route>
        </Routes>
      </div>
      </Router> */}
    </>
  );
}

export default App;
